import React from "react";
import PropTypes from 'prop-types'
import OnlineUtils from "eshop/utils/OnlineUtils";
/**
 * Renders an attribute box
 */

var tooltipPlaceholder = "TOOLTIP-ID-PLACEHOLDER"

export default class Attribute extends React.Component {

    componentDidMount() {
        if (hasTooltip(this.props.value)) {
            OPL.UI.loadModules(document.getElementById(this.tooltipId()), [{
                path: 'core/modules/tooltips',
                options: { "mouseoverMinWidth": 0, "triggerEvent": "mouseover" }
            }]);
        }
    }


    componentWillUnmount() {
        if (hasTooltip(this.props.value)) {
            OPL.UI.stopModules(this.refs.container);
         }
    }

    tooltipId(){
        if(this.tooltipIdValue === undefined)
            this.tooltipIdValue = OnlineUtils.generateUniqeHtmlId("featureTooltip_")
        return this.tooltipIdValue
    }

    render() {
        return renderMethods()[this.props.type](this.props, this.tooltipId.bind(this))
    }
}

Attribute.propTypes = {
    type: PropTypes.string.isRequired, //static || bar
    value: PropTypes.string.isRequired,
    technicalValue: PropTypes.number,
    icon: PropTypes.string,
}

function renderMethods() {
    return {
        static: renderStatic,
        bar: renderBar
    }
}

function renderBar(props) {

    let value = props.value
    let technicalValue = props.technicalValue
    let barDescription = props.config.dataBarDescription

    let innerHtmlForValue = {__html: value}
    let innerHtmlForDescription = {__html: barDescription}

    let iconPart =
        <div className="opl-box__icon-list__icon">
            <span
                className="opl-box__dataplan-badge opl-box__dataplan-badge--break-medium u-medium-vertical-middle"
                dangerouslySetInnerHTML={innerHtmlForValue}>
            </span>
        </div>

    let barPart =
        <div className="opl-box__icon-list__text">
            <div title={technicalValue + "%"} className="o-progress opl-progress u-spacing u-small-hide">
                <div style={{ width: technicalValue + "%" }} className="o-progress__bar">
                    <span className="u-acc-hide">{technicalValue}%</span>
                </div>
            </div>
            <span dangerouslySetInnerHTML={innerHtmlForDescription}></span>
        </div>


    return renderLi(
        props.box ? boxClass(): "",
        [iconPart, barPart],
        props.hide,
        props.border
        )

}

function boxClass(){
   return "opl-box__icon-list__item"
}

function tableClass(){
   return "u-table"
}

function renderStatic(props, tooltipId) {

    let icon = props.icon
    let value = props.value

    if (hasTooltip(value)) {
        value = value.replace(tooltipPlaceholder, tooltipId())
    }

    var li = valueAsLi(value)


    function liClass(){
        if(li){
           return li.className
        } else {
           let c =
               [(props.box  ? boxClass()   : ""),
               (props.table ? tableClass() : "")]
           return c.join(" ")
        }
    }

    function renderLiWrapper(children){
         return renderLi( liClass(), children, props.hide, props.border, props.fixer)
    }

    if (icon) {
        return renderLiWrapper(  [renderIcon(icon), renderHtml(value)])
    } else if(li) {
        return renderLiWrapper( [<div dangerouslySetInnerHTML={{ __html: li.value }}></div>])
    } else {
        return renderLiWrapper(  [<div dangerouslySetInnerHTML={{ __html: value }}></div>])
    }
}

function renderIcon(icon) {

    if (icon) {
        let icon_ = icon.toLowerCase().replace('_', '-');
        let istyle = "g-icon g-icon--" + icon_ + " g-icon--only u-medium-vertical-middle";

        return (
            <div className="opl-box__icon-list__icon">
                <span aria-hidden="true" className={istyle}></span>
            </div>
        )
    }
}

//value
function valueAsLi(li){
    let liElement = '<li'

    li = li.trim()
    if (!li.startsWith(liElement)){
        return
    }
    //class extraction
    let s0 = (s) => s.substring(0,s.indexOf('>'))
    let s1 = (s) => s.substring(s.indexOf("class"))
    let s2 = (s) => s.substring(s.indexOf('"')+1)
    let s3 = (s) => s.substring(0,s.indexOf('"'))

    //inner element
    let e0 = (s) => s.substring(s.indexOf('>')+1)
    let e1 = (s) => s.substring(0,s.lastIndexOf('<'))

    return {
        className: [li].map(s0).map(s1).map(s2).map(s3)[0],
        value: [li].map(e0).map(e1)[0]
    }
}

function renderHtml(value) {

    let innerHtml = { __html: value }

    return (
        <div className="opl-box__icon-list__text" dangerouslySetInnerHTML={innerHtml}></div>
    )
}

function renderLi(classStr, children, hide, border, fixer) {

    function fixerWrapper(fixer,children){
        return(
            <div className={"u-animate-height " + fixer}>
                 <div className="js-height-sensitive-element">
                      {children}
                 </div>
            </div>
            )
    }

    let classes =
    classStr +
    (hide ? " u-hide":"") +
    (border ? " u-border u-no-border-l u-no-border-r u-no-border-t":"")

    let inside = null
    if(fixer){
        inside =fixerWrapper(fixer,children)
    }else{
        inside = children
    }

    return (
        <li ref='container' className={classes}>
            {inside}
        </li>
    )
}

function hasTooltip(value) {
    return value.indexOf(tooltipPlaceholder) >= 0
}


